/* HEADER */
.Header {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-around;
  width: 100vw;
  align-items: center;
  /* padding: 0px 30px; */
  box-shadow: 0 4px 6px -7px black;
  background-color: #009fd9;
}
.Nav {
  position: relative;
}
.Ul {
  display: flex;
  list-style: none;
}
.Li {
  margin: 0 10px;
  padding: 5px;
  border-radius: 5px;
}
.Li:hover {
  background-color: #028abb;
}
.Li a {
  text-decoration: none;
  font-size: 20px;
  font-weight: 300;
  color: #fff;
}
.LiButton {
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer;
}

/*  LOGIN USER */

.LoginUser {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
}
.InLogin {
  margin: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  /* height: 200px;
  width: 200px; */
  padding: 25px;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-shadow: 0 1px 0 rgba(0,0,0,.25);
  background-color: #fff;
}
/* FORM */
.Form {
  position: relative;
  display: flex;
  flex-direction: column;
}
.Form p {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
}
.Form input {
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #c7c7c7;
  font-size: 15px;
}
.ButtonLogin {
  margin-top: 10px;
  position: relative;
  background-color: #009fd9;
  border-radius: 5px;
  box-shadow: 0 1px 0 rgba(0,0,0,.25);
  color: #fff;
  padding: 10px;
  text-align: center;
  border: 0;
  font-size: 15px;
  cursor: pointer;
}
.ButtonRegister {
  position: relative;
  background-color: #fff;
  color: #009fd9;
  border: 0;
  font-size: 15px;
  cursor: pointer;
}
.ButtonRegister:hover {
  color: #0183b3;
}