/* HEADER */
.Header {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 55px;
    align-items: center;
    /* padding: 0px 30px; 
    box-shadow: 0 4px 6px -7px black;*/
    background-color: #fff;
    border-bottom: 1px solid #e2e2e2;
  }
  .Nav {
    position: relative;
  }
  .Ul {
    display: flex;
    list-style: none;
  }
  .Li {
    margin: 0 10px;
    padding: 5px;
    border-radius: 5px;
    
  }
  .Li a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 300;
    color: #606060;
  }
  .Li a:hover {
    color: #009fd9;
  }
  .LiButton {
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
  }