.App {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: #f8f8f8;
}
.InApp {
  height: 100%;
  width: 100%;
  display: flex;
}
.InAppLeft {
  position: relative;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  display: flex;
  flex-direction: column;
  background-color: #009fd9;
  color: #fff;
  box-shadow: 12px 1px 21px -14px rgba(0,0,0,0.5);
  z-index: 2;
}
.InAppLeft p {
  padding-left: 25px;
}
/* RIGHT HALF APP */
.InAppRight {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #fff;
  overflow: hidden; /*needed for messenger!*/
}

.Nav {
  position: relative;
}
.Ul {
  display: flex;
  list-style: none;
  padding-left: 10px;
  flex-direction: column;
}
.Li {
  margin: 0 10px;
  padding: 5px;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.LeftAppMenu {
  position: absolute;
  display: flex;
  bottom: 30px;
}
.Li img {
  margin-right: 10px;
}
.HomeIcon, .SignOutIcon {
  height: 20px;
  width: 20px;
}