.Users {
    position: relative;
    display: flex;
    flex-direction: column;
}
.UsersName {
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    align-items: center;
    padding-left: 20px;
}
.UsersIcon {
    height: 20px;
    width: 20px;
}
.UserIcon {
    height: 15px;
    width: 15px;
}
.Users h3 {
    padding-left: 10px;
}
.Users input {
    width: 180px;
    padding: 8px;
    border-radius: 5px;
    border: 1px solid #c7c7c7;
    font-size: 15px;
    cursor: pointer;
}
.UsersInput {
    position: relative;
    margin: auto;
    padding: 0 5px;
}
.EachUser {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 50px;
}
.EachUser:hover, .EachUser:target, .EachUser:focus {
    background-color: #00729b;
    cursor: pointer;
}
.EachUser p {
    margin: 5px 0;
    padding-left: 10px;
}