.Desktop {
    position: relative;
    display: flex;
    width: 320px;
    height: 100%;
    flex-direction: column;
    padding: 10px;
    background-color: #e3e3e3;
}
.BeforeChart {
    position: relative;
    width: 100%;
    /* height: 200px; */
    height: auto;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
}
.InChart {
    display: flex;
    justify-content: center;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
}
.InChartButton {
    padding: 5px;
    /* border: 1px solid #fff; */
    border-radius: 5px;
    color: #fff;
    background-color: #009fd9;
    margin: 0 15px 0 15px;
    box-shadow: 1px 3px 10px -5px #000;
    cursor: pointer;
}