.Desktop {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    
}
.List {
    position: relative;
    height: 100%;
    min-width: 220px!important;
    width: 220!important;
    overflow: hidden;
    border-right: 1px solid #e2e2e2;
}
.Recepti {
    position: relative;
    display: flex;
    flex-direction: column;
}
.EachRecept {
    position: relative;
    border-bottom: 1px solid #e2e2e2;
    color: #606060;
    cursor: pointer;
    padding: 0 10px;
}
.EachRecept:hover {
    color: #009fd9;
    background-color: #e2e2e2;
}
.EachTop {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.List h4 {
    color: #606060;
    margin: 5px 0;
}
.List p {
    padding-left: 25px;
    color: #606060;
    margin: 0px 0 ;
}