*,
  *:before,
  *:after {
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
}
/* *{border: 1px solid red;} */
html {
    scroll-behavior: smooth; /*adds smooth scrolling when using navigation instead if instant teleport :)*/
                             /* u can assign this ti personal element if choose ?? w3cschool :)*/
}
.Header {
    position: fixed;
    z-index: 10;
    display: flex;
    justify-content: space-around;
    width: 100vw;
    height: 60px; /*test - loosing padding under 600px !!*/
    align-items: center;
    /* padding: 0px 30px; */
    box-shadow: 0 4px 6px -7px black;
    background-color: #009fd9;
    padding: 10px;
}
.LogoDiv {
    position: relative;
    display: flex;
}
.LogoImg {
    height: 25px;
    width: 25px;
}
.HamMenu {
    display: none;
    position: fixed;
    height: 80%;
    width: 80%;
    background-color: #009fd9;
    color: #fff;
    z-index: 2; /*to show over page*/
    box-shadow: 0 0px 6px 0px black;
    top: 60px;
}
.UlHam {
    display: flex;
    list-style: none;
    margin: 0;
    flex-direction: column;
    padding-left: 0;
}
.LiHam {
    margin: 0 10px;
    padding: 5px;
    border-radius: 5px;
    padding: 15px 15px;
}
.LiHam:hover {
    background-color: #028abb;
}
.LiHam a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 300;
    color: #fff;
}
.HamDiv {
    display: none;
    position: relative;
    padding: 10px;
    /*border: 1px solid black; test*/
    cursor: pointer;
}
.HamIcon {
    height: 25px;
    width: 25px;
}
.Nav {
    position: relative;
}
.Ul {
    display: flex;
    list-style: none;
    margin: 0;
}
.Li {
    margin: 0 10px;
    padding: 5px;
    border-radius: 5px;
}
.Li:hover {
    background-color: #028abb;
}
.Li a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 300;
    color: #fff;
}
/** IMAGE SECTION **/
.SectionOne {
    position: relative;
    display: flex;
    max-height: 70vh;
    min-height: calc(800px - 30vh); /*test*/
    max-width: 1200px;
    /* width: 80%; */
    margin: auto;
}
.Background {
    position: relative;
    background-color: rgb(230, 225, 221);
    width: 100%;
    align-items: center;
    display: flex;
}
.Background img {
    height: 100%;
    width: 100%;
}
.ColBack {
    position: absolute;
    display: flex;
    margin: auto 60px;
    flex-direction: column;
    max-width: 350px;
    width: 50%;
}
.InBack {
    position: relative;
}
.InBack p {
    font-size: 40px;
    font-weight: 700;
}
.PicButton {
    position: relative;
    width: 115px;
    text-align: center;
    margin: auto;
    color: white;
    margin-left: 10px;
    text-decoration: none;
    background-color: #009fd9;
    height: calc(2.875rem + 2px);
    padding: 1rem 3rem;
    font-size: 1.5rem;
    line-height: 1.5;
    border-radius: 5px;
    -webkit-transition-duration: 0.2s; /* Safari */
    transition-duration: 0.2s;
}
.PicButton:hover {
    background-color: #0289b9;
}

/*  SECTION NEW */
.SectionNew {
    position: relative;
    display: flex;
    min-height: 580px;
    padding: 0 0 50px 0;
    /* width: 100vw; */
    background-color:#f3f3f3;
}
.InNew {
    position: relative;
    display: flex;
    margin: auto;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.InNew h2 {
    font-size: 35px;
    font-weight: 700;
}
.Cards {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    justify-content: center;
}
.Card {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 10px;
    height: 400px;
    width: 25%;
    min-width: 200px;
    max-width: 300px;
    background-color: #fff;
    margin: 10px;
    box-shadow: 0 0 2rem rgba(0,0,0,.1);
}
.Card p {
    font-size: 23px;
    font-weight: 700;
    text-align: left;
    margin: 0;
}
.Card h5 {
    font-size: 10px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 5px;
}
.Card img {
    /* height: 100%;
    width: 100%; 
    min-height: 150px;
    min-width: 220px; */
    max-height: 150px;
    max-width: 220px;
    min-width: 100%;
    min-height: 150px;
}
/*  SECTION EXPLAIN */
.SectionExplain {
    position: relative;
    display: flex;
    min-height: 505px;
    padding: 0 0 50px 0;
    /* width: 100vw; */
    margin: 50px auto;
    background-color: #fff;
}
.InExplain {
    position: relative;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    flex-direction: row;
    text-align: center;
    width: 900px;
}
.InExpRow {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 400px;
    padding: 0px 20px;
    justify-content: center;
    margin-right: 50px;
}
.InExpRow h2 {
    font-size: 35px;
    font-weight: 700;
}
.RowImage {
    width: 400px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 50px;
}
.InExplain img {
    position: relative;
    width: 50%;
    height: 90%;
    box-shadow: 0px 8px 3px -8px rgba(0,0,0,0.5);
}
.InExplain img:nth-child(1) {
    top: -50px;
}
.InExplain img:nth-child(2) {
    left: -50px;
}
.InExplain img:nth-child(3) {
    left: -320px;
    top: 50px;
    z-index: 2;
}
/*  SECTION ABOUT */
.SectionAbout {
    position: relative;
    display: flex;
    min-height: 505px;
    padding: 0 0 50px 0;
    /* width: 100vw; */
    background-color: #fff;
}
.InNew {
    position: relative;
    display: flex;
    margin: auto;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.InNew h2 {
    font-size: 35px;
    font-weight: 700;
}
.CardsAbout {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    justify-content: center;
}
.CardAbout {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 10px;
    /* height: 280px;  ZAENKRAT NERABBIMO*/
    width: 25%;
    min-width: 200px;
    max-width: 300px;
    background-color: #fff;
    margin: 10px;
}
.CardAbout p {
    font-size: 20px;
    font-weight: 500;
    text-align: left;
    margin: 0;
}
.CardAbout h5 {
    font-size: 20px;
    font-weight: 700;
    text-align: left;
    margin-bottom: 5px;
}
.InCardAbout {
    display: flex;
}
.Image {
    height: 35px;
    width: 35px;
}
/*   TRY   */

.SectionTry {
    position: relative;
    display: flex;
    padding: 50px 0 80px 0;
    /* width: 100vw; */
    background-color: #009fd9;
    min-height: 300px;
    justify-content: center;
    align-items: center;
}
.InTry {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 20px;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.InTry h2 {
    font-size: 35px;
    font-weight: 700;
    color: #fff
}
.TryButton {
    position: relative;
    width: 115px;
    text-align: center;
    margin: auto;
    color: white;
    margin-left: 10px;
    text-decoration: none;
    /* background-color: #009fd9; */
    height: calc(2.875rem + 2px);
    padding: 1rem 3rem;
    font-size: 15px;
    font-weight: 700;
    line-height: 1.5;
    border-radius: 5px;
    border: 1px solid #fff;
    transition: all .2s ease-in-out;
}
.TryButton:hover {
    border-radius: 0;
    transform: scale(0.8);
    background-color: #028abb;
}

/* FOOTER */

.Footer {
    position: relative;
    /* width: 100vw; */
    align-items: center;
    background-color: rgb(245, 245, 245);
}
.InFooter {
    display: flex;
    flex-direction: column;
    /* padding: 20px 70px 0; */
    padding: 30px 0;
    width: 800px;
    margin: auto;
}
.InF {
    display: flex;
    justify-content: space-between;
    /*align-items: baseline; */
    flex-direction: row;
    width: 100%;
    box-sizing: inherit;
}
.InFLeft {
    display: flex;
    flex-direction: row;
}
.UlF {
    display: flex;
    list-style: none;
    flex-direction: column;
    padding-left: 40px;
}
.UlF:nth-child(1) {
    padding-left: 0;
}
.UlF h2 {
    margin-top: 0;
}
.LiF {
    /* margin: 0 5px;
    padding: 5px; */
    padding: 5px 0;
}
.LiF:hover {
    text-decoration: underline;
}
.LiF a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 300;
    color: rgb(0, 0, 0);
}
.ImageF {
    height: 35px;
    width: 35px;
    cursor: pointer;
}
.ImageD {
    height: 45px;
    width: 120px;
    border-radius: 5px;
    cursor: pointer;
}
.UlFIcons {
    display: flex;
    list-style: none;
    flex-direction: row;
}
.Pravice {
    padding: 30px 0;
    /* padding-left: 40px; */
}

/* MEDIA */
@media (max-width: 800px) {
    /** IMAGE SECTION **/
    .SectionOne {
        min-height: 50vh;
        width: 100%;
    }
    .SectionOne .InBack p {
        font-size: 25px;
        font-weight: 700;
    }
    .SectionOne .PicButton {
        width: 90px;
        background-color: #009fd9;
        font-size: 1rem;
    }
    /*SECTION ABOUT*/
    .SectionAbout .InNew h2 {
        font-size: 25px;
        font-weight: 700;
    }
    /*SECTION EXPLAIN*/
    .InExplain {
        width: 100%;
    }
    .RowImage {
        display: none;
    }
    .InExpRow {
        margin-right: 0px;
    }
    .InExpRow h2 {
        font-size: 20px;
        font-weight: 700;
    }
    /*SECTION FOOTER*/
    .Footer .InF {
        flex-direction: column;
    }
    .Footer .InFooter {
        flex-direction: column;
        width: 100%;
    }
    .Footer .InFLeft {
        flex-direction: column;
    }
    .InFLeft:nth-child(1) {
        padding-left: 40px;
    }
    .UlF {
        padding-left: 0;
    }
    .Pravice {
        padding-left: 40px;
    }
}
@media (max-width: 600px) {
    .Header {
        justify-content: space-between;
        padding: 10px 30px;
    }
    .Nav {
        display: none;
    }
    .HamMenu {
        display: flex;
    }
    .HamDiv {
        display: flex;
    }
    /*section explain*/
    .SectionExplain {
        padding: 0;
        margin: 0;
        min-height: 55vh;
    }
    .InExpRow {
        padding: 0 70px;
    }
}