.Desktop {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    
}
.List {
    position: relative;
    height: 100%;
    min-width: 220px!important;
    width: 220!important;
    overflow: hidden;
    border-right: 1px solid #e2e2e2;
    background-color: #009fd9;
    padding-top: 20px;
}
.DesktopIcons, .DesktopIconsSmall {
    /* margin: 0 10px; 
    padding: 5px; */
    padding-left: 20px;
    /* border-radius: 5px; */
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    display: flex;
    align-items: center;
}
.DesktopIconsSmall {
    padding-left: 45px;
}
.Icons {
    height: 20px;
    width: 20px;
}
.IconsS {
    height: 15px;
    width: 15px;
}
.List h4 {
    /* color: #606060; */
    color: #fff;
    cursor: pointer;
    margin: 0;
    padding: 10px;
}
.List p {
    /* color: #606060; */
    color: #fff;
    margin: 5px 0 ;
    cursor: pointer;
    margin: 0px;
    font-size: 15px;
    font-weight: 400;
    padding: 5px 0 5px 10px;
}
.DesktopIcons:hover, .DesktopIconsSmall:hover {
    background-color: #00729b;
}
.DesktopIcons:focus, .DesktopIconsSmall:focus {
    background-color: #00729b;
}
